global.rootRequire = function (name) {
  return require("".concat(__dirname, "/packages/").concat(name, "/src/index.js"));
};

var _require = require('./packages/caver-core'),
    packageInit = _require.packageInit,
    providers = _require.providers;

var Klay = require('./packages/caver-klay');

var Net = require('./packages/caver-net');

var Method = require('./packages/caver-core-method');

var middleware = require('./packages/caver-middleware');

var utils = require('./packages/caver-utils');

var formatters = require('./packages/caver-core-helpers').formatters;

var helpers = require('./packages/caver-core-helpers');

var _require2 = require('./package.json'),
    version = _require2.version;

function Caver(provider, net) {
  var _this = this;

  this.use = middleware.registerMiddleware.bind(middleware); // sets _requestmanager etc

  packageInit(this, [provider, net]);
  this.version = version;
  this.utils = utils;
  this.formatters = formatters;
  this.helpers = helpers;
  this.Method = Method; // ex) call `onit.klay.property` || `onit.klay.method(...)`

  this.klay = new Klay(this);
  this.middleware = middleware; // overwrite package setProvider

  var setProvider = this.setProvider;

  this.setProvider = function (provider, net) {
    setProvider.apply(_this, [provider, net]);

    _this.klay.setProvider(provider, net);

    return true;
  };
}

Caver.providers = providers;
module.exports = Caver;
module.exports.formatters = formatters;